<template>
  <v-app id="inspire">
    <v-app-bar
      app
      dark
    >
      <v-icon style="margin-right: 10px;">fa-robot</v-icon>
      <v-toolbar-title v-if="$route.path !== '/'" style="cursor: pointer;" @click="$router.push('/')">Newser</v-toolbar-title>
      <v-toolbar-title v-else style="cursor: pointer;">Newser</v-toolbar-title>
      <v-icon v-if="started && onLine" style="margin-left: 10px; color:green">fa-circle</v-icon>
      <v-icon v-if="stopped && onLine" style="margin-left: 10px; color:yellow">fa-circle</v-icon>
      <v-icon v-if="!onLine" style="margin-left: 10px; color:red">fa-circle</v-icon>
      <v-spacer></v-spacer>
      <p v-if="$route.path !== '/stat'" style="font-size: 20px; margin-top: 15px; margin-right: 20px; cursor: pointer" @click="$router.push('/stat')">Статистика</p>
      <p v-else style="font-size: 20px; margin-top: 15px; margin-right: 20px; cursor: pointer">Статистика</p>
      <p style="font-size: 20px; margin-top: 15px;">{{ getUser }}</p>
      <v-btn style="margin-left:10px; width: 20px; background-color:white; color: black"
        @click="$store.dispatch('logout')"
      ><v-icon>fa-sign-out-alt</v-icon></v-btn>
    </v-app-bar>
    <v-overlay
      :value="starting"
      opacity="0.95"
    ><p style="font-size: 25px;">Подготовка к запуску!</p>
      <div align="center"><v-progress-circular
        width="7"
        size="80"
        color="white"
        indeterminate
      ></v-progress-circular></div>
    </v-overlay>
    <main role="main" class="container">
      <v-overlay
        :value="!isLoggedIn"
        opacity="0.95"
      >
        <p style="
        width: 250px;
        font-size: 20px;
        text-align: center;
        ">Чтобы управлять поисковым ботом необходимо авторизоваться в системе!</p>
        <v-text-field placeholder="Логин" v-model="login"></v-text-field>
        <v-text-field type="password" placeholder="Пароль" v-model="password"></v-text-field>
        <v-btn
          style="width: 100%"
          color="orange lighten-2"
          @click="signIn"
        >
          Войти
        </v-btn>
      </v-overlay>
      <router-view :messages="messages" :isOnline="onLine"/>
      <v-footer
      style="margin-top: 50px;"
      fixed
      class="font-weight-medium"
    >
      <v-col
        class="text-center"
        cols="12"
      >
        <v-btn :disabled="started || !onLine" @click="startBot" style="margin-left:10px; background-color:green; color:white"><v-icon>fa-play</v-icon></v-btn>
        <v-btn :disabled="stopped || !onLine" @click="stopBot" style="margin-left:10px; background-color:red; color:white"><v-icon>fa-stop</v-icon></v-btn>
        <v-btn :disabled="messages.length == 0" @click="messages = []" style="margin-left:10px; background-color:blue; color:white"><v-icon>fa-broom</v-icon></v-btn>
        <v-btn style="margin-left:10px; background-color:grey; color:white"
        @click="$router.push('/config')"
        ><v-icon>fa-cogs</v-icon></v-btn>
      </v-col>
    </v-footer>
    </main>
  </v-app>
</template>

<script>
  import {mapGetters} from 'vuex'

  export default {
    props: {
      source: String,
    },

    computed: {
      ...mapGetters(['getUser']),

      isLoggedIn() {

        for (let key in this.getUser) {
          return true
        }
        return false
      },
    },

    data: () => ({
      messages: [],
      
      stopped: false,
      started: false,
      onLine: false,

      stopping: false,
      starting: false,

      login: '',
      password: ''

    }),

    sockets: {

      disconnect: function() {
        this.onLine = false
      },
      
      captcha: function() {
        this.$socket.emit('captcha')
      },

      connect: function () {
          this.onLine = true
          this.stopped = true
          this.onLine = true
          this.started = false
          this.stopped = false
      },

      message: function(message) {

        if (message.code === 0) {
          this.stopped = true
          this.started = false
        } else {
          this.stopped = false
          this.started = true
        } 

        if (message.message === 'Бот остановлен!') {
          this.stopping = false
        }

        if (message.code == 1) {
          this.starting = false
        }

        this.messages.unshift(message)
        }
    },

     methods: {

      startBot() {

        this.messages.unshift({
          message: 'Запустить бота',
          sender: 'Оператор',
          subtitle: 'Команда'
        })

        this.$socket.emit('startBot')
        this.starting = true
      },

      stopBot() {

        this.messages.unshift({
          message: 'Остановить бота',
          sender: 'Оператор',
          subtitle: 'Команда'
        })

        this.$socket.emit('stopBot')
        this.stopping = true
      },

      signIn() {
        const user = {
          login: this.login,
          password: this.password
        }

        this.$store.dispatch('login', user)
      }
    }
  }
</script>